import { HEX_CHARACTERS } from './constants';

export abstract class Nonce {
  // --- Note ---
  // nonce — «number that can only be used once»
  // is a hex valid string for the one-time use, which helps
  // tracking mechanism to differ one tracking event from another
  protected nonce: string;

  constructor() {
    this.nonce = this.getNonce();
  }

  private getNonce(): string {
    const hexLength = HEX_CHARACTERS.length;
    let nonce: string = '';

    for (let i = 0; i < hexLength; i++) {
      const randomNumber = Math.floor(Math.random() * hexLength);
      nonce += HEX_CHARACTERS.substring(randomNumber, randomNumber + 1);
    }

    return nonce;
  }
}
